/* Layout general */
body, .app {
  background: #0c243d;
  color: #fff;
  font-family: 'Segoe UI', sans-serif;
  margin: 0;
  padding: 0;
}

/* Navbar */
.nav-bar {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  background: #2f3d5e;
  padding: 8px 6px;
  gap: 12px;
  font-size: 13px;
}
.nav-bar button {
  background: none;
  color: #bbb;
  border: none;
  font-size: 13px;
  padding: 4px 6px;
  cursor: pointer;
}
.nav-bar button.active {
  color: #d6b86a;
  font-weight: 600;
}

/* Títulos */
.recommendations h2,
.section h2 {
  padding: 10px 16px;
  font-size: 16px;
  display: flex;
  align-items: center;
  gap: 6px;
}

/* Recomendaciones */
.dish-card {
  display: flex;
  background: #2f3d5e;
  margin: 10px 16px;
  padding: 10px;
  border-radius: 16px;
  gap: 12px;
  align-items: center;
}
.dish-card img {
  width: 58px;
  height: 58px;
  border-radius: 10px;
  object-fit: cover;
}
.card-content {
  flex: 1;
}
.card-content h4 {
  margin: 0;
  color: #d6b86a;
  font-size: 15px;
}
.card-content p {
  margin: 4px 0;
  color: #ddd;
  font-size: 13px;
  line-height: 1.2;
}
.card-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 6px;
}
.price {
  font-size: 14px;
  color: #ccc;
}

/* Botón AR */
.ar-btn {
  background: #f15454;
  color: white;
  padding: 5px 10px;
  font-size: 12px;
  border-radius: 20px;
  border: none;
  font-weight: bold;
  cursor: pointer;
}
.ar-btn:hover {
  background: #d94141;
}

/* Secciones normales */
.subsection h3 {
  color: #d6b86a;
  margin: 18px 16px 8px;
  font-size: 15px;
}

.dish-item {
  display: flex;
  justify-content: space-between;
  background: #2f3d5e;
  margin: 8px 16px;
  padding: 12px;
  border-radius: 16px;
  align-items: center;
}
.dish-item img {
  width: 50px;
  height: 50px;
  border-radius: 8px;
  object-fit: cover;
  margin-right: 12px;
}
.dish-info {
  flex: 1;
}
.dish-info h4 {
  color: #fff;
  margin: 0;
  font-size: 14px;
}
.dish-info p {
  margin: 2px 0 0;
  color: #eee;
  font-size: 12.5px;
}
